import React from 'react';
import {
  Flex,
  Container,
  Heading,
  Stack,
  Text,
  Button,
  Icon,
  useToast,
} from '@chakra-ui/react';
import { ExternalLinkIcon } from '@chakra-ui/icons';
import { Link } from 'react-router-dom';
import { useState as useHookState } from '@hookstate/core';
import store from '../../store';

export default function LandingPageCallToAction() {
  const { userDetails } = useHookState(store);

  const toast = useToast();

  return (
    <Container maxW={'5xl'}>
      <Stack
        textAlign={'center'}
        align={'center'}
        spacing={{ base: 8, md: 10 }}
        py={{ base: 20, md: 28 }}
      >
        <Heading
          fontWeight={600}
          fontSize={{ base: '3xl', sm: '4xl', md: '6xl' }}
          lineHeight={'110%'}
        >
          Create a personal AMA dashboard in a{' '}
          <Text as={'span'} color={'red.400'}>
            few clicks
          </Text>
        </Heading>
        <Text color={'gray.500'} maxW={'3xl'}>
          A more effective, engaging way to connect to the people who follow
          you.
        </Text>
        <Stack spacing={6} direction={'row'}>
          <Button
            rounded={'full'}
            px={6}
            colorScheme={'red'}
            bg={'red.400'}
            _hover={{ bg: 'red.500' }}
          >
            See demo <ExternalLinkIcon mx={2} />
          </Button>
          {userDetails.get() ? (
            <Button rounded={'full'} px={6} as={Link} to={'/dashboard'}>
              Go to dashboard
            </Button>
          ) : (
            <Button
              rounded={'full'}
              px={6}
              onClick={() =>
                toast({
                  title: 'Not authenticated',
                  description: 'Please log in first!',
                  status: 'error',
                  duration: 9000,
                  isClosable: true,
                })
              }
            >
              Go to dashboard
            </Button>
          )}
        </Stack>
        <Flex w={'full'}>
          <Illustration
            height={{ sm: '12rem', lg: '16rem' }}
            mt={{ base: 12, sm: 16 }}
          />
        </Flex>
      </Stack>
    </Container>
  );
}

